import React from 'react';
import { EmblaCarousel } from '../../components/EmblaCarousel';
import { PartnersCarousel } from '../../components/PartnersCarousel';
import { YandexMap } from '../../components/YandexMap';

import styles from './Home.module.scss';

const workpartners = [
  {
    title: 'ArtGroup',
    logoUrl: './png/logo/ArtGroup.png',
  },
  {
    title: 'MyBisnes',
    logoUrl: './png/logo/MyBisnes.png',
  },
  {
    title: 'MyDocument',
    logoUrl: './png/logo/MyDocument.png',
  },
  {
    title: 'RioFitness',
    logoUrl: './png/logo/RioFitness.png',
  },
];

const uspartners = [
  {
    title: 'Grass',
    logoUrl: './png/logo/Grass.png',
  },
  {
    title: 'Karcher',
    logoUrl: './png/logo/Karcher.png',
  },
  {
    title: 'Bahler',
    logoUrl: './png/logo/Bahler.png',
  },
  {
    title: 'Planet',
    logoUrl: './png/logo/Planet.png',
  },
  {
    title: 'ProBrite',
    logoUrl: './png/logo/ProBrite.png',
  },
  {
    title: 'Floor',
    logoUrl: './png/logo/Floor.png',
  },
];

const advantages = [
  {
    url: '/png/leaf.png',
    text: ' ',
  },
  {
    url: '/png/clock.png',
    text: ' ',
  },
  {
    url: '/png/handshake.png',
    text: ' ',
  },
  {
    url: '/png/muscles.png',
    text: ' ',
  },
  {
    url: '/png/star.png',
    text: '',
  },
  {
    url: '/png/bank.png',
    text: ' ',
  },
];

function Home() {
  const [selectValues, setSelectValues] = React.useState({
    firstSelect: 'house',
    secondSelect: 'fast',
  });
  const handleChangeSelect = (e) => {
    setSelectValues((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onCalcBtn = () => {
    console.log(selectValues);
  };

  return (
    <div className={styles.main}>
      <div
        className={styles.bgImage}
        style={{ backgroundImage: 'url(/jpg/girl.jpg)' }}
      >
        <div className={styles.calc}>
          <select
            name='firstSelect'
            value={selectValues.firstSelect}
            onChange={handleChangeSelect}
          >
            <option value='office'></option>
            <option value='gymnasium'></option>
            <option value='house'>  </option>
          </select>
          <select
            name='secondSelect'
            value={selectValues.secondSelect}
            onChange={handleChangeSelect}
          >
            <option value='general'></option>
            <option value='fast'></option>
            <option value='repair'> </option>
          </select>
          <button onClick={onCalcBtn}></button>
        </div>
      </div>
      <div className={styles.wrapper}>
        <h2 className={styles.advantagesTitle}> </h2>
        <div className={styles.advantages}>
          {advantages.map((obj) => (
            <div className={styles.advantage} key={obj.text}>
              <div className={styles.icon}>
                <img src={obj.url} alt='Image' />
              </div>
              <h3>{obj.text}</h3>
            </div>
          ))}
        </div>
      </div>
      <div
        className={styles.bgBenefitImage}
        style={{ backgroundImage: 'url(/jpg/benefit.jpg)' }}
      >
        <div className={styles.bgColor}>
          <div className={styles.benefitText}>
            <h2>129</h2>
            <span> </span>
          </div>
          <div className={styles.benefitText}>
            <h2>194</h2>
            <span> </span>
          </div>
          <div className={styles.benefitText}>
            <h2>217</h2>
            <span> </span>
          </div>
          <div className={styles.benefitText}>
            <h2>71</h2>
            <span>  </span>
          </div>
        </div>
      </div>
      <div className={styles.about}>
        <h2>    ,     .</h2>
        <p>
                 .  
            .     ,  
               .   
           :     ,   
             ;      
             . ,  , 
          ,       .
            :     !
        </p>
      </div>
      <div className={styles.reviews}>
        <h2> </h2>
        <EmblaCarousel />
      </div>
      <div className={styles.callBackInfo}>
        <h2>  ?</h2>
        <button> </button>
      </div>
      <div className={styles.partnersWork}>
        <h2>   </h2>
        <PartnersCarousel partners={workpartners} />
      </div>
      <div className={styles.partnersWork}>
        <h2> </h2>
        <PartnersCarousel partners={uspartners} />
      </div>
      <div className={styles.works}>
        <h2> </h2>
        <EmblaCarousel worksmap />
      </div>
      <YandexMap />
    </div>
  );
}

export default Home;
