import React from 'react';

import styles from './QnA.module.scss';

const tabs = [
  {
    id: 1,
    title: '     ?',
    description:
      '      -    .    -   .',
  },
  {
    id: 2,
    title: '       ?',
    description:
      '     ,         . .          - ,    .',
  },
  {
    id: 3,
    title:
      '      ""     ?',
    description:
      '        .  -       -   .',
  },
  {
    id: 4,
    title: '          ?',
    description: '  "  ".',
  },
  {
    id: 5,
    title: '       ?  ?',
    description:
      '    .       .     " "-         ,    , ,     .',
  },
  {
    id: 6,
    title: '   ,   ?',
    description:
      '.          .',
  },
];

function QnA() {
  const [activeId, setActiveId] = React.useState(1);

  const onClickLabel = (tabId) => {
    setActiveId(tabId);
  };

  return (
    <div className={styles.wrapper}>
      <h2>-</h2>
      <div className={styles.tabs}>
        {tabs.map((tab) => (
          <div
            key={tab.id}
            className={`${styles.tab} ${
              tab.id === activeId ? styles.active : ''
            }`}
          >
            <label
              className={styles.tabTitle}
              onClick={() => onClickLabel(tab.id)}
            >
              {tab.title}
            </label>
            <div className={styles.tabContent}>
              <p>{tab.description}</p>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

export default QnA;
